<template>
    <div class="box">
        <indexHeader></indexHeader>
        <div class="account">
            <div class="topcontent ft20 white">
                <span class=""><span class="baseColor">{{$t('asset.assets')}}></span> {{$t('asset.address_withdraw')}}</span>
            </div>
            <div class="content">
               <div class="con_box white">
                  
                   <div class="contentBK pdlr20 pdtb20 ft12">
                       <div class="flex alcenter ">
                           <span class="w10 mr20">{{$t('asset.currency')}}</span>
                           <span class="flex1 mr20">{{$t('asset.address_width')}}</span>
                           <span class="flex1">{{$t('set.code')}}</span>
                       </div>
                       <div class="mt10">
                            <div class="flex alcenter">
                                <select name="" id="" class="w10 mr20 " v-model="addId" @change='setCoin'>
                                    <option :value="this.$t('asset.currency')" >{{$t('asset.currency')}}</option>
                                    <option :value="item.id" v-for="(item,index) in coins" :key="index" >{{item.name}}</option>
                                </select>
                                <input type="text" class="flex1 mr20" :placeholder="this.$t('asset.enterAdderses')"  v-model="address">

                                <div class="code-box flex alcenter">
                                    <input type="text" class="input-main input-content" maxlength="16" v-model="code">
                                    <button type="button" class="redBg getCode" @click="sendCode">{{resetSeconds}}</button>
                                </div>
                                <!-- <input type="text" class="flex1 " :placeholder="this.$t('asset.pbeizhu')" v-model="notes"> -->
                            </div>
                            <div class="add">
                                <div class="baseBtn" @click="addAddress">{{$t('asset.add')}}</div>
                            </div> 
                       </div>
                   </div>
               </div>
               <!-- <div class="con_box mt15">
                   <div class="account_title ttBk flex">
                       <div class="ml20">{{$t('asset.addressList')}}</div>
                   </div>
                   <div class="contentBK  fColor3 pdlr20 pdtb20 ft12">
                       <div class="flex alcenter">
                           <span class="flex1">{{$t('asset.currency')}}</span>
                           <span class="flex1">{{$t('asset.address_width')}}</span>
                           <span class="flex1">{{$t('asset.beizhu')}}</span>
                           <span class="flex1">{{$t('td.do')}}</span>
                       </div>
                       <div class="mt10" v-for="(item,index) in list" :key="index">
                            <div class="flex alcenter">
                                <span class="flex1">
                                    {{nowCoin}}
                                </span>
                                <span class="flex1">{{item.address}}</span>
                                <span class="flex1">{{item.notes}}</span>
                                <span class="flex1" @click="delAddress(item.id,index)">{{$t('asset.delete')}}</span>
                            </div>
                       </div>
                        <div class="none color1 tc" v-if="!list.length" style="padding: 20px 0">
                            {{$t('asset.norec')}}
                        </div>
                       
                   </div>
               </div> -->
            </div>
        </div>
    </div>
</template>
<script>
import indexHeader from "@/view/indexHeader";
export default {
  name: "finanrec",
  data() {
    return {
      token: "",
      lang: "",
      coins:[],
      nowCoin:'',
      list:[],
      address:'',
      notes:'',
      code:'',
      userid:'',
      addId:this.$t('asset.currency'),
      resetSeconds: this.$t('set.sendCode'),
      contract_address:''
    };
  },
  components: {
    indexHeader
  },
  created() {
    this.getCoins();
    this.getinfo();
    // this.getAddress()
  },
  methods: {
   
     getwallet(currency, callback) {//币种详情
        var that = this;
      $.ajax({
        type: "POST",
        url: "/api/" + "wallet/get_info",
        data: {
          currency: currency
        },
        dataType: "json",
        async: true,
        beforeSend: function beforeSend(request) {
          request.setRequestHeader("Authorization", that.token);
        },
        success: function(res) {
          if (res.type == "ok") {
            console.log(res.message)
             that.contract_address = res.message.contract_address;
             that.getAddress();
            //  console.log(that.$data)
            //  if (typeof callback == 'function') {
            //    callback(res);
            //  }
          } else {
          }
        }
      });
    },
    getinfo() {
      this.token = localStorage.getItem("token") || "";

      this.$http({
        url: "/api/user/info",
        headers: { Authorization: this.token }
      }).then(res => {
        console.log(res)
        if(res.data.type == 'ok'){
            this.userid = res.data.message.id;

        }
      });
    },
    getCoins() {//币种列表
      this.token = localStorage.getItem("token") || "";

      this.$http({
        url: "/api/wallet/currencylist",
        headers: { Authorization: this.token }
      }).then(res => {
        if(res.data.type == 'ok'&&res.data.message.length != 0){
            this.coins = res.data.message;
            // that.getAddress();
        }
      });
    },
    //获取提币地址
		getAddress() {
      var that = this;
      $.ajax({
          url: 'https://www.elamavip.com/walletMiddle/GetDrawAddress',
          type: "GET",
          dataType: "json",
          async: false,
          data: {
            user_id: this.userid,
            coin_name: this.nowCoin,
            contract_address: this.contract_address,
          },
          success: function(res) {
            if(res.code == 0) {
              console.log(res.data.address)
              that.address = res.data.address;
            } else {

            }
          }
        });
    },
    setCoin(e){
        var that = this;
        if(this.addId != this.$t('asset.currency')){
            let index = e.target.selectedIndex;
            let opt = e.target.querySelectorAll('option')[index];
            that.nowCoin = opt.text;
            let coinindex = index-1;
            let currency = that.coins[coinindex].id

            that.address = "";
            that.getwallet(currency);
            // that.getinfo()
            // that.getwallet(currency, function (res) {
            //   that.contract_address = res.message.contract_address
            //   
            // });
            
      
        }
    },
   
    // 发送验证码
    sendCode(e) {
      var that = this;
      var user_id = that.userid
      this.$http({
        url: "https://www.elamavip.com/walletMiddle/SendVerificationcode?user_id=" + user_id,
        method: "get",
        data: {
          user_id:that.userid
        }
      }).then(res => {
        console.log(res)
        if(res.data.code == 0) {
          layer.msg('发送成功');
          var times = 30
          var timer = setInterval(() => {
            times--;
            $('.getCode').text(times + 's');
            $('.getCode').attr('disabled', true);
            if(times <= 0) {
              clearInterval(timer);
              $('.getCode').text('获取验证码');
              $('.getCode').removeAttr('disabled');
            }
          }, 1000);
        } else {
          layer.msg(res.data.msg)
        }
      });
    },
    delAddress(id,index){
        this.$http({
        url: "/api/wallet/deladdress",
        method:'post',
        data:{address_id:id},
        headers: { Authorization: this.token }
      }).then(res => {
        if(res.data.type == 'ok'){
            this.list.splice(index,1)
             layer.msg(res.data.message);
        }
      });
    },
    addAddress(){
      var that = this;
      console.log(this.contract_address)
        if(this.addId == this.$t('asset.currency')){
            layer.msg(this.$t('asset.selectCurrency'));return;
        }
        if(this.address == ''){
            layer.msg(this.$t('asset.enterAdderses'));return;
        } 
        if(this.code == ''){
            layer.msg(this.$t('set.enterCode'));return;
        } else {
            var obj = {
              user_id: this.userid,
              address: this.address,
              coin_name: this.nowCoin,
              contract_address: this.contract_address,
              verificationcode: this.code,
              t: Date.parse(new Date()) / 1000,
            };
            var obj_str = JSON.stringify(obj);
            console.log(obj_str)
            console.log($.md5(obj_str + 'abcd4321'))
            console.log(location.origin)
            $.ajax({
              url: 'https://www.elamavip.com/walletMiddle/BindDrawAddress',
              type: "POST",
              dataType: "json",
              async: true,
              data: {
                data: obj_str,
                sign: $.md5(obj_str + 'abcd4321')
              },
              success: function(res) {
                console.log(res)
                if(res.code == 0) {
                  layer.msg('绑定成功');
                  that.address = "";
                  that.code = "";
                  that.getAddress()
                } else {
                  layer.msg(res.msg)
                }
              }
            });
        }
    }
  }
};
</script>

<style lang="scss" scoped>
.box .account {
  width: 1200px;
  margin: 0 auto 82px;
  margin-top: 30px;
  
}
.topcontent {
  padding: 22px 30px;
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 5px;
  background-color: #1a243b;
}
.account_title {
  padding: 15px 20px;
  color: #61688a;
}
select {
  background-color: transparent;
  padding: 10px;
  color: #61688a;
  font-size: 18px;
  border-radius: 3px;
  border: 1px solid #ccc;
}
select.ft14 {
  font-size: 14px;
}
select > option {
  padding: 10px 0;
  background-color: transparent;
}
input {
  padding: 10px;
  border: 1px solid #ccc;
  font-size: 18px;
  color: #61688a;
  border-radius: 3px;
  background-color: transparent;
  outline: none;
}
.add {
  text-align: right;
  margin-top: 20px;
}
.add .baseBtn {
  width: 200px;
}
.code-box {
  border-radius: 2px;
  /* border: 1px solid #ccc; */
  width: 500px;
}
.code-box input {
  width: 320px;
  /* border: none; */
}
.code-box button {
  padding: 0 20px;
  height: 44px;
  line-height: 44px;
  width: 150px;
  border-radius: 3px;
  cursor: pointer;
  background: #7a98f7;
  color: #fff
}
</style>





