<template>
    <div class="apply-box">
        <div class="apply">
            <div class="flex alcenter">
                <h1 class="ft20">{{$t('apply.aly2')}}</h1>
                <span class="ft18 curPer baseColor ml30" @click="goapply">{{$t('apply.aly1')}}</span>
            </div>
            <div class="flex column alcenter  fColor3">
                <ul class="flex column ft14">
                    <li class="flex alcenter between">
                        <span>{{$t('apply.aly13')}}</span>
                        <span>{{$t('apply.aly14')}}</span>
                        <span>{{$t('apply.aly15')}}</span>
                        <span>{{$t('apply.aly16')}}</span>
                        <span>{{$t('apply.aly17')}}</span>
                    </li>
                    <li class="flex alcenter between" v-for="(item,index) in lists" :key="index">
                        <span>{{item.token_name}}</span>
                        <span>{{item.created_at}}</span>
                        <span>{{item.status_name}}</span>
                        <span class="baseColor curPer" @click="godetail(item.id)">{{$t('apply.aly18')}}</span>
                        <span class="baseColor curPer" @click="godelete(item.id)">{{$t('asset.delete')}}</span>
                    </li>
                </ul>
                <div class="tc pointer ptb20 ft12" v-if="more" @click="getMore">{{$t('td.more')}}</div>
                <div v-else class="tc ptb20 ft12">{{$t('td.nomore')}}</div>
            </div>
        </div>
    </div>
</template>
<script>
import "@/lib/clipboard.min.js"
export default {
    data (){
        return{
            more:false,
            page:1,
            limit:20,
            lists:[]
        }
    },
    beforeCreate () {
        document.querySelector('html').setAttribute('style', 'background-color:#e5ebf5;')
    },
    beforeDestroy () {
        document.querySelector('html').removeAttribute('style')
    },
    mounted(){
        document.querySelector('html').setAttribute('style', 'background-color:#e5ebf5;')
        this.getList();
    },
    methods:{ 
        goapply(){
           this.$router.push({ name: "apply" });
        },
        getList(){
            var that =this;
            this.$http({
                url: '/api/'+'on_currency/list?page='+that.page,
                method:'get',
                data:{},  
                headers: {'Authorization':  localStorage.getItem('token')},    
            }).then(res=>{
                if(res.data.type=="ok"){
                    
                    var list = res.data.message.data;
                    that.lists = that.lists.concat(list);
                     if(list.length>9){
                        that.more=true;
                    }else{
                        that.more=false;
                    }
                }else{
                    layer.msg(res.data.message)
                }
            }).catch(error=>{
                // console.log(error)
            })
        },
         getMore(){
            this.page+=1;
            this.getList();
        },
        godetail(id){
            this.$router.push({
                name:'apply_detail',
                query:{
                id:id
                }
            })
        },
        godelete(id){
             this.$http({
                url: '/api/'+'on_currency/del',
                method:'post',
                data:{id:id},  
                headers: {'Authorization':  localStorage.getItem('token')},    
            }).then(res=>{
                if(res.data.type=="ok"){
                //    layer_loading()
                   setTimeout(function () {
                        location.reload();
                    }, 1000)
                }else{
                    layer.msg(res.data.message)
                }
            }).catch(error=>{
                // console.log(error)
            })
        }
    },
    
}

</script>
<style scoped lang='scss'>

.apply-box{
    padding-bottom: 50px;
}
.apply{
    width: 100%;
    max-width: 1200px;
    min-height: 200px;
    background: #fff;
    margin: 30px auto 0;
    padding:30px 20px;
}
ul{
    width: 100%;
    margin-top: 40px;
}
ul li{
    height: 50px;
    line-height: 50px;
    width: 100%;
    border-bottom: 1px dashed #ccc;
}
li span{
    width: 20%;
    text-align: center;
}
</style>





