<template>
  <div id="legaltrade-box" class="">
    <div class="content fColor1">
      <ul class="list">
        <!-- <li class="flex between">
          <span>{{$t('fat.business8')}}</span>
          <input v-model="application.account" type="text">
        </li> -->
        <li class="flex between">
          <span>{{$t('fat.business9')}}</span>
          <input v-model="application.name" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business10')}}</span>
          <input v-model="application.phone" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business11')}}</span>
          <select id="currency">
            <option
              style="height:50px;"
              v-for="item in currencyList"
              :key="item.id"
              :value="item.id"
            >{{item.name}}</option>
          </select>
        </li>
        <!-- <li class="flex between">
				<span>商家余额</span>
				<input v-model="application.balance" type="text">
        </li>-->
        <li class="flex between">
          <span>{{$t('fat.business12')}}</span>
          <input v-model="application.wechatNick" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business13')}}</span>
          <input v-model="application.wechatAccount" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business14')}}</span>
          <input v-model="application.alipayNick" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business15')}}</span>
          <input v-model="application.alipayAccount" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business16')}}</span>
          <select id="bank">
            <option v-for="item in bankList" :key="item.id" :value="item.id">{{item.name}}</option>
          </select>
        </li>
        <li class="flex between">
          <span>{{$t('fat.business17')}}</span>
          <input v-model="application.bankAccount" type="text">
        </li>
        <li class="flex between">
          <span>{{$t('fat.business18')}}</span>
          <input v-model="application.bankName" type="text">
        </li>
      </ul>
      <div class="uploads flex ftw ml15 mr10">
        <div class="uploads-list tc">
          <p class="title ft16">{{$t('fat.business19')}}</p>
          <div>
            <img :src="src1" alt class="alipay-img">
            <!-- <img class="alipay-img" src="images/myimg/688660269851391423.png" alt> -->
            <input type="file" id="alipay" @change="uploads(1)">
          </div>
        </div>
        <div class="uploads-list tc">
          <p class="title ft16">{{$t('fat.business20')}}</p>
          <div>
            <img :src="src2" alt class="wechat-img">
            <!-- <img  src="images/myimg/688660269851391423.png" alt> -->
            <input type="file" name id="wechat" @change="uploads(2)">
          </div>
        </div>
      </div>
      <!-- 保存按钮 -->
      <div class="save ft15 ftw">
        <button class="ftw" type="button" @click="sumbitApply()">{{$t('fat.business21')}}</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkboxValue: false,
      token: "",
      bankList: "",
      checkboxValue: false,
      currencyList: [],
      bankName: "选择银行",
      currencyName: "选择资产",
      src1: "../../static/imgs/addimg.png",
      src2: "../../static/imgs/addimg.png",
      application: {
        // account: "",
        name: "",
        phone: "",
        balance: "",
        wechatNick: "",
        wechatAccount: "",
        alipayNick: "",
        alipayAccount: "",
        bankAccount: "",
        bankName: ""
      }
    };
  },
  created() {
    let that = this;
    let token = window.localStorage.getItem("token") || "";
    if (token) {
      that.token = token;
    }
    this.$http({
      url: "/api/seller_apply_info",
      headers: {
        Authorization: that.token
      }
    }).then(res => {
      if (res.data.type == "ok") {
        that.bankList = res.data.message.banks;
        that.currencyList = res.data.message.currency;
      }
    });
  },
  beforeCreate () {
			document.querySelector('html').setAttribute('style', 'background-color:#f7f7f7;')
		},
		beforeDestroy () {
			document.querySelector('html').removeAttribute('style')
		},
  methods: {
    sumbitApply() {
      let that = this;
      let currencyId = $("#currency option:selected").val();
      let bankId = $("#bank option:selected").val();
      // if (!that.application.account) {
      //   layer.msg("请输入交易账户");
      //   return false;
      // }
      // if (!that.application.name) {
      //   layer.msg("请输入名称");
      //   return false;
      // }
      // if (!that.application.phone) {
      //   layer.msg("请输入商家电话");
      //   return false;
      // }
      // if (!currencyId) {
      //   layer.msg("请选择资产");
      //   return false;
      // }
      // if(!that.application.balance){
      // 	layer_msg('请输入商家余额');
      // 	return false;
      // }
      // if (!that.application.wechatNick) {
      //   layer.msg("请输入微信昵称");
      //   return false;
      // }
      // if (!that.application.wechatAccount) {
      //   layer.msg("请输入微信账户");
      //   return false;
      // }
      // if (!that.application.alipayNick) {
      //   layer.msg("请输入支付宝昵称");
      //   return false;
      // }
      // if (!that.application.alipayAccount) {
      //   layer.msg("请输入支付宝账户");
      //   return false;
      // }
      // if (!bankId) {
      //   layer.msg("请选择银行");
      //   return false;
      // }
      // if (!that.application.bankAccount) {
      //   layer.msg("请输入银行账户");
      //   return false;
      // }
      // if (!that.application.bankName) {
      //   layer.msg("请输入开户行");
      //   return false;
      // }
      // if (that.src1 == "../../static/imgs/addimg.png") {
      //   layer.msg("请上传支付宝付款码");
      //   return false;
      // }
      // if (that.src2 == "../../static/imgs/addimg.png") {
      //   layer.msg("请上传微信付款码");
      //   return false;
      // }
      var src11 = '';
      var src22 = '';
      if (that.src1 == "../../static/imgs/addimg.png") {
        src11 = '';
      }else{
        src11 = that.src1;
      }
      if (that.src2 == "../../static/imgs/addimg.png") {
        src22 = '';
      }else{
        src22 = that.src2;
      }
      let data = {
        // account_number: that.application.account,
        name: that.application.name,
        mobile: that.application.phone,
        currency_id: currencyId,
        // seller_balance:that.application.balance,
        wechat_nickname: that.application.wechatNick,
        wechat_account: that.application.wechatAccount,
        ali_nickname: that.application.alipayNick,
        ali_account: that.application.alipayAccount,
        bank_id: bankId,
        bank_account: that.application.bankAccount,
        bank_address: that.application.bankName,
        alipay_collect: src11,
        wechat_collect: src22
      };
      var i = layer.load();
      this.$http({
        url: "/api/seller_apply",
        data: data,
        method: "post",
        headers: {
          Authorization: that.token
        }
      }).then(res => {
        layer.msg(res.data.message);
         layer.close(i);
        if (res.data.type == "ok") {
          setTimeout(function() {
            that.$router.push({path:'/LegalTrade'});
          }, 1000);
        }
      });
    },
    // 上传图片
    uploads(type) {
      let that = this;
      var formData = new FormData();
      if (type == 1) {
        formData.append("file", $("#alipay")[0].files[0]);
      } else {
        formData.append("file", $("#wechat")[0].files[0]);
      }
      var i = layer.load();
      $.ajax({
        url: "/api/" + "upload",
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(msg) {
          layer.close(i);
          if (msg.type == "ok") {
            if (type == 1) {
              that.src1 = msg.message;
            } else {
              that.src2 = msg.message;
            }
          } else {
            layer.msg(msg.message);
            if (type == 1) {
              that.src1 = "../../static/imgs/addimg.png";
            } else {
              that.src2 = "../../static/imgs/addimg.png";
            }
          }
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
$purple: #bcac7a;
#legaltrade-box{
  width: 100%;
  margin: 0 auto 30px;
	background-color: #1a243b;
	min-height: 650px;
	padding-bottom: 30px;
	-webkit-box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  box-shadow: 0 0 29px 0 rgba(32, 36, 48, 0.08);
  padding-top: 30px;
}
.content {
  width: 950px;
  margin: 100px auto;
  .list {
    margin: 13px;
  }
  .list li {
    line-height: 40px;
    margin-bottom: 15px;
  }
  .list span {
    width: 150px;
    text-align: right;
    display: block;
  }
  .list input {
    width: 760px;
    line-height: 40px;
    background-color: #1f2740;
    border-radius: 4px;
    padding: 0 6px;
    border: 1px solid #07417b;
    color: #fff;
  }
  .list select {
		width: 760px;
		-moz-appearance:none;
    -webkit-appearance:none;
		appearance:none;
		line-height: 40px;
		padding:0 10px;
		font-size: 16px;
    background-color: #1f2740;
    border-radius: 4px;
        border: 1px solid #07417b;
        color: #fff;
        
		option{
			padding: 10px 0;
		}

  }
  .uploads {
    margin-left: 170px;
    margin-top: 30px;
  }
  .uploads-list {
    width: 120px;
    margin-right: 20px;
    position: relative;
  }
  .uploads-list img {
    width: 120px;
    height: 120px;
  }
  .uploads-list p {
    line-height: 1.5;
    margin-bottom: 10px;
  }
  .uploads-list input {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 120px;
    height: 120px;
    opacity: 0;
  }
  .save {
    background: #bcac7a;
    line-height: 40px;
    width: 760px;
    color: #fff;
    cursor: pointer;
    text-align: center;
    border-radius: 6px;
    margin-top: 50px;
    margin-left: 170px;
    button {
      width: 100%;
      background-color: rgba(0, 0, 0, 0);
      color: #fff;
    }
  }
}
select::-ms-expand {display: none;}
</style>



