<template>
    <div class="apply-box">
        <div class="apply">
            <div class="flex alcenter">
                <h1 class="ft20">{{$t('apply.aly19')}}</h1>
                <span class="ft18 curPer baseColor ml30" @click="goapply">{{$t('apply.aly1')}}</span>
            </div>
            <ul class="flex column w100">
                <li class="flex column w100">
                    <span>{{$t('apply.aly3')}}</span>
                    <p>{{data.currency_official_website}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly4')}}</span>
                    <p>{{data.white_paper_website}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly5')}}</span>
                    <p>{{data.intro}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly6')}}</span>
                    <p>{{data.github_link}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly7')}}</span>
                    <p>{{data.twitter_or_telegram_link}}</p>
                </li>

                <li class="flex column w100">
                    <span>{{$t('apply.aly13')}}</span>
                    <p>{{data.token_name}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly8')}}</span>
                    <p>{{data.token_symbol}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly12')}}</span>
                    <img :src="data.logo" alt="" class="logo">
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly9')}}</span>
                    <p>{{data.contract_address}}</p>
                </li>
                <li class="flex column w100">
                    <span>{{$t('apply.aly10')}}</span>
                    <p>{{data.block_browser}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import "@/lib/clipboard.min.js"
export default {
    data (){
        return{
            id:'',
            data:'',
        }
    },
    beforeCreate () {
        document.querySelector('html').setAttribute('style', 'background-color:#e5ebf5;')
    },
    beforeDestroy () {
        document.querySelector('html').removeAttribute('style')
    },
    created(){
       this.id=this.$route.query.id;
       console.log(this.id)
    },
    mounted(){
        document.querySelector('html').setAttribute('style', 'background-color:#e5ebf5;')
        this.getinfo();
    },
    methods:{ 
        goapply(){
           this.$router.push({ name: "apply" });
        },
        getinfo(){
            // this.id=this.$route.query.id;
            this.$http({
                url: '/api/'+'on_currency/info?id='+this.id,
                method:'get',
                headers: {'Authorization':  localStorage.getItem('token')},    
            }).then(res=>{
                if(res.data.type=="ok"){
                    this.data = res.data.message;
                    
                }else{
                    layer.msg(res.data.message)
                }
            }).catch(error=>{
                // console.log(error)
            })
        },
    },
    
}

</script>
<style scoped lang='scss'>

.apply-box{
    padding-bottom: 50px;
}
.apply{
    width: 100%;
    max-width: 1200px;
    min-height: 200px;
    background: #fff;
    margin: 30px auto 0;
    padding:30px 20px;
}
ul li span{
    font-size: 14px;
    color: #357ce1;
    margin: 15px 0 10px;
}
ul li p{
    line-height: 20px;
    font-size: 12px;
}
.logo{
    width: 100px;
    height: 100px;
}
</style>





