<template>
<div class="forget-box">
    <indexHeader></indexHeader>
    <div class="contentBK">
        <div class="content-wrap">
            <div class="account">
                <div class="main" v-if="!showReset">
                    <p class="main_title">重置密码</p>
                    <div class="register-input">
                        <span class="register-item">{{$t('ctc.account')}}</span>
                        <div class="country-box">
                          <div class="flex">
                            <div class="flex between alcenter brd plr20" v-if="isMb">
                              <input class="lf_input pointer" type="text" v-model="areaCode"  readonly @click="showCode= !showCode">
                              <i class="iconfont icon-xiala ft12" v-if="!showCode"></i>
                              <i class="iconfont icon-shangla_icon ft12" v-else></i>
                            </div>
                            <input type="text" v-model="account_number" id="account" class="phone codeinput plr20" autocomplete="off">
                            <!-- <input type="text" class="input-main input-content" maxlength="20" v-model="account_number" id="account"> -->
                          </div>
                          <ul class="curPer input_select scroll" v-if="showCode">
                            <li class="flex between alcenter bdb" v-for="(item,index) in country" @click="getCode(item.area_code)">
                              <p>{{item.name_en}}</p>
                              <p class="tr">{{item.area_code}}</p>
                            </li>
                          </ul>   
                        </div>
                    </div>
                     <div class="register-input code-input" >
                        <span class="register-item">{{$t('set.code')}}</span>
                        <div class="code-box">
                          <input type="text" class="input-main input-content" maxlength="16" v-model="phoneCode" id="pwd" >
                          <button type="button" class="pointer"  @click="setTime">{{$t('set.getCode')}}</button>
                        </div>
                    </div>
                    <div style="margin-top: 10px;">
                        <span class="register-item"></span>
                        <button class="register-button curPer" type="button" @click="check">确认</button>
                        
                    </div>
                   
                </div>
                <div class="main" v-if="showReset">
                    <div class="main_title">{{$t('set.setPsw')}}</div>
                    <div class="register-input">
                        <span class="register-item">{{$t('set.enterPsw')}}</span>
                        <input type="password" class="input-main input-content"  v-model="password" id="pwd">
                    </div>
                    <div class="register-input">
                        <span class="register-item">{{$t('set.enterPswagain')}}</span>
                        <input type="password" class="input-main input-content"  v-model="re_password" id="repwd">
                    </div>
                    <button class="register-button curPer" type="button" @click="resetPass" style="margin-top:20px">{{$t('td.confirm')}}</button>
                </div>
            </div>
        </div>
    </div>
    <indexFooter></indexFooter>
</div>

</template>

<script>
import country from '../lib/country.js'
import indexHeader from "@/view/indexHeader";
import indexFooter from "@/view/indexFooter";
export default {
  components: { indexHeader, indexFooter },
  data() {
    return {
      isMb: true,
      account_number: "",
      phoneCode: "",
      showReset: false,
      password: "",
      re_password: "",
      country:country,
      areaCode:country[0].area_code,
      showCode:false
    };
  },
  created() {
    
  },
  methods: {
    getCode(code){
      this.areaCode=code;
      this.showCode=false;
    },
    sendCode(url) {
      console.log(url)
      this.$http({
        url: "/api/" + url,
        method: "post",
        data: {
          user_string: this.account_number,
          // type: "forget"
          scene:'reset_password',
          lang:this.$utils.lg(),
          country_code: this.areaCode
        },
        headers:{
          Authorization: localStorage.getItem("token")
        }
      }).then(res => {
        layer.msg(res.data.message);
      });
    },
    setTime(e) {
      var that = this;
      if (e.target.disabled) {
        return;
      } else {
        var reg = /^1[3456789]\d{9}$/;
        var url = "sms_send";
        var emreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
        if (this.account_number == "") {
          layer.tips(this.$t('set.enterAccount'), "#account");
          return;
        }
        if (emreg.test(this.account_number)) {
          url = "sms_mail";
          this.isMb = false;
        }else{
          url = "sms_send"; 
          this.isMb = true;
        }
        this.sendCode(url);
        var time = 60;
        var timer = null;
        timer = setInterval(function() {
          e.target.innerHTML = time + 's';
          e.target.disabled = true;
          if (time == 0) {
            clearInterval(timer);
            e.target.innerHTML = that.$t('set.sendCode');
            console.log(that.$t('set.sendCode'))
            e.target.disabled = false;
            return;
          }
          time--;
        }, 1000);
      }
    },
    check() {
      var reg = /^1[345678]\d{9}$/;
      var emreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      let user_string = this.account_number;
      var isMobile = reg.test(user_string);
      var isEmail = emreg.test(user_string);
      var url = "user/check_mobile";
      var data = {};
      data.user_string = this.account_number;
      data.country_code = this.areaCode;
      data.lang = this.$utils.lg();
      if (user_string == "") {
        layer.tips(this.$t('set.enterAccount'), "#account");
        return;
      } else if (this.phoneCode == "") {
        layer.tips(this.$t('set.enterCode'), "#pwd");
        return;
      } else if (isEmail) {
        url = "user/check_email";
        data.email_code = this.phoneCode;
      // } else if (isMobile) {
      } else if (!isEmail){
        url = "user/check_mobile";
        data.mobile_code = this.phoneCode;
      } else {
        layer.tips(this.$t('set.accountFalse'), "#account");
        return;
      }

      this.$http({
        url: "/api/" + url,
        method: "post",
        data: data
      }).then(res => {
        layer.msg(res.data.message);
        if (res.data.type == "ok") {
          this.showReset = true;
          // window.location.href = "resetpass.html?user_string=" + names + "&" + "code=" + verify;
          // this.$router.push({path:'/resetPwd',params:{user_string:user_string,code:this.phoneCode}})
        }
      });
    },
    resetPass() {
      if (this.password == "") {
        layer.msg(this.$t('set.enterPsw'));
        return;
      } else if (this.re_password == "") {
        layer.msg(this.$t('set.enterPswagain'));
        return;
      } else if (this.password !== this.re_password) {
        layer.msg(this.$t('set.pswFalse'));
        return;
      } else {
        let data = {
          account: this.account_number,
          password: this.password,
          repassword: this.re_password,
          code: this.phoneCode,
          lang:this.$utils.lg(),
          country_code: this.areaCode
        };
        this.$http({
          url: "/api/user/forget",
          method: "post",
          data: data,
        
        }).then(res => {
          layer.msg(res.data.message);
          if (res.data.type == "ok") {
            this.$router.push("/components/login");
          }
        });
      }
    }
  }
};
</script>
<style scoped>
.content-wrap {
  background: url(../assets/images/bg_login.png) center bottom 316px repeat-x,
    -webkit-linear-gradient(top, #21263f, #171c2b);
}
.account {
  width: 1200px;
  margin: 0 auto;
  padding-top: 93px;
  overflow: hidden;
  min-height: 880px;
}
.main {
  position: relative;
  padding: 0 0 60px 30px;
}
.main_title {
  font-size: 36px;
  color: #c7cce6;
}
.register-item {
  display: block;
  height: 22px;
  color: #61688a;
  font-size: 12px;
}
.register-input {
  position: relative;
  margin-top: 20px;
}
.input-box {
  position: relative;
  margin-top: 40px;
}
.input-main {
  width: 520px;
  min-height: 46px;
  border: 1px solid #4e5b85;
  padding: 0 20px;
  color: #000;
  font-size: 14px;
  border-radius: 3px;
  background-color: #ecf5ff;
}
.icon {
  width: 48px;
  height: 48px;
  line-height: 48px;
  border-right: 1px solid #2e3753;
  position: absolute;
  top: 0;
}
.login-btn {
  width: 420px;
  margin-top: 40px;
  background: #5697f4;
  font-size: 16px;
  border-radius: 4px;
  color: #fff;
  line-height: 48px;
  cursor: pointer;
}
.noaccount {
  color: #fff;
}
.register-button {
  width: 200px;
  display: inline-block;
  line-height: 46px;
  background-color: #7a98f7;
  border-radius: 4px;
  color: #fff;
  border: none;
}
.have-account {
  font-size: 14px;
  display: inline-block;
  margin-left: 30px;
}
.right-tip {
  position: absolute;
  left: 620px;
  top: 70px;
  line-height: 24px;
  padding-right: 50px;
  margin-top: 10px;
  font-size: 14px;
  color: #61688a;
}
.code-box {
  width: 520px;
  border: 1px solid #ccc;
  background: #ecf5ff;
}
.code-box .input-main {
  width: 419px;
  border: none;
}
.code-box button {
  border: none;
  border-left: 1px solid #7a98f7;
  line-height: 47px;
  color: #fff;
  background: #7a98f7;
  width: 94px;
}
.phone{
  width: 360px!important;
  border-left: none;
}
.lf_input{
  width: 80px;
  border: none;
  height: 44px;
  border-radius: 0;
  padding: 0;
}
input{
  color: #000;
  text-fill-color: #000;
  -webkit-text-fill-color: #000;
  background: transparent
}
.country-box{
  width: 520px;
  min-height: 46px;
  border: 1px solid #4e5b85;
  padding: 0 20px;
  color: #000;
  font-size: 14px;
  border-radius: 3px;
  background-color: #ecf5ff;
  position: relative;
}
.country-box{
  padding: 0;
}
.brd{border-right: 1px solid #4e5b85}
.plr20{
  padding-left: 10px;
  padding-right: 10px;
}
.codeinput{
  border: none;
  height: 44px;
}
.input_select{
  width: 100%;
  position: absolute;
  z-index: 5;
  left: 0;
  right: 0;
  top: 46px;
  max-height: 250px;
  overflow-y: auto;
  box-shadow: 0 2px 6px 0 #000;
  border-color: #d1d3df;
  background-color: #fff;
  color: #9194a4;
}
.input_select li{
  padding: 15px;
}
.icon {
  width: 48px;
  height: 48px;
  line-height: 48px;
  border-right: 1px solid #2e3753;
  position: absolute;
  top: 0;
}
</style>



